export default {
	data() {
		return {
			
		};
	},
	props: ['position','dictValue','count'],
	watch: {
	    position: {
	      handler () {
	        this.$refs.customOverlay.reload()
	      },
	      deep: true
	    }
	},
	methods:{
		    handleClick () {
				uni.showToast({
					icon:'none',
					title:'点击！'
				})
		    },
		    draw ({el, BMap, map}) {
		      const {lng, lat} = this.position
		      const pixel = map.pointToOverlayPixel(new BMap.Point(lng, lat))
		      el.style.left = pixel.x - 40 + 'px'
		      el.style.top = pixel.y - 20 + 'px'
		    }
	}
}
